	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

body {
	width: 700px;
	margin-left: auto;
	margin-right: auto;
}

/* INICIO CABEÇALHO */
.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 95%;
	margin-right: auto;
	margin-left: auto;
	margin-top: 2.5em;
}

.menu-icon-container {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	height: 43px;
	width: 80px;
	cursor: pointer;
	border-radius: 2px;
}

.fa-arrow-left {
	margin-top: 2px;
	width: 80px;
	font-weight: 600;
	font-size: 36px;
	color: #2f435a;
	filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
}

.fa-arrow-left:hover {
	font-weight: 600;
	color: #fdce6d;
	transition: 0.2s all;
}

.menu-icon-container>button {
	background-color: transparent;
	width: 80px;
	height: 43px;
	outline: 0;
	border: none;
	cursor: pointer;
}

.title-principal {
	display: block;
	align-items: center;
	color: #2f435a;
	font-size: 24px;
}

.logo {
	display: flex;
	border: none;
	filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
}

.title-pagina {
	display: none;
}

/* FIM CABEÇALHO */


/* INICIO FORMULÁRIO */

form {
	width: 95%;
	margin-left: auto;
	margin-right: auto;
}

/* INICIO CLIENTE */
.label-title-cliente,
.label-title-resp {
	display: flex;
	margin-right: auto;
	margin-left: auto;
	margin-top: 2.5em;
	width: 100%;
	height: 1.875em;
	border-radius: 3.125em;
	align-items: center;
	background-color: #2f435a;
	box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
}

.text-title-cliente,
.text-title-resp {
	display: flex;
	height: 1.25em;
	padding-top: 0.0625em;
	margin-left: 0.625em;
	align-items: center;
	color: #fdce6d;
	font-size: 1.25em;
}

.name-client-container,
.email-client-container,
.fone-client-container {
	display: flex;
	justify-content: space-between;
	margin-left: auto;
	margin-right: auto;
	margin-top: 0.625em;
	align-items: center;
	width: 97%;
	height: 1.875em;
	gap: 0.15625em;
}

.fa-user {
	color: #2f435a;
	font-size: 1.6em;
	padding-left: 3px;
	filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
}

.fa-envelope {
	color: #2f435a;
	font-size: 1.78125em;
	filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
}

.fa-phone {
	color: #2f435a;
	font-size: 1.7em;
	filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
}

#nameClient,
#emailClient,
#foneClient {
	display: flex;
	justify-content: center;
	align-items: center;
	line-height: 1.875em;
	margin-left: 0.08em;
	padding-top: 0.0625em;
	padding-left: 0.5em;
	width: 40.25em;
	height: 1.875em;
	color: rgb(47, 67, 90);
	font-size: 1em;
	border: none;
	background-color: #fff1d4;
	box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
	border-radius: 0.5em;
	outline: 0;
}

#nameClient::placeholder,
#emailClient::placeholder,
#foneClient::placeholder,
#text-value-msg::placeholder,
#text-value-resp::placeholder {
	color: #2f435a8a;
}

#msg {
	display: flex;
	width: 100%;
	margin-left: auto;
	margin-right: auto;
}

#text-value-resp,
