	display: flex;
	margin-left: auto;
	margin-right: auto;
	width: 97%;
	height: 6.25em;
	margin-top: 0.625em;
	padding: 0.3125em;
	color: #2F435A;
	background-color: #FFF1D4;
	border: none;
	outline: none;
	border-radius: 0.5em;
	font-size: 1em;
	resize: none;
	box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
}

/* FIM FORMULÁRIO */


/* INICIO AÇÕES */

#actions {
	display: flex;
	margin-top: 1.875em;
	width: 43.75em;
	margin-right: auto;
	margin-left: auto;
	flex-direction: row;
	justify-content: center;
	gap: 1.25em;
}

#inserir-resposta,
#excluir-mensagem,
#enviar-resposta {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 100px;
	width: 11em;
	height: 2em;
	background-color: #2F435A;
	box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
	outline: 0;
	border: none;
	border-radius: 3.125em;
	color: #FDCE6D;
	font-size: 1em;
	transition: 0.5s;
	cursor: pointer;
}

#inserir-resposta:hover,
#excluir-mensagem:hover,
#enviar-resposta:hover {
	display: flex;
	background-color: #FDCE6D;
	box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
	outline: 0;
	border: none;
	border-radius: 3.125em;
	color: #2F435A;
	transition: 0.5s;
	cursor: pointer;
}

